//-----------------------------------
import axios from 'axios'
var apiRoot = 'http://localhost:1338/api/';

export default {   
    employees: {
        list(id) { return axios.get(apiRoot + 'employeesbymanager/' + id); },
        listManagers() { return axios.get(apiRoot + 'managers'); },       
        create(obj) { return axios.post(apiRoot + 'employee', obj); },       
    },
    roles: {
        listRoles() { return axios.get(apiRoot + 'roles'); },
    },   
}