<template>
    <div class="container-fluid">
        <!--<div class="row" v-if="isLoading">
        <spinner :msg="'loading, please wait...'"></spinner>
        v-show="!isLoading"
    </div>-->
        <div class="card">
            <div class="card-heading">
                <div class="row">
                    <div>
                        <h1 class="lead">Add New Employee</h1>
                    </div>
                </div>
                <div class="row">
                    <div class="col-sm-6">
                        <div class="form-group">
                            <label>Manager</label>
                            <select class="form-control" v-model="manager" @change="managerChange()">
                                <option v-for="x in managers" :key="x.EmployeeID" :value="x">{{x.DisplayName}}</option>
                            </select>
                        </div>
                    </div>
                </div>
            </div>
            <div class="card-body">
                <div class="row">
                    <div class="col-sm-12 table-responsive">
                        <table class="table table-striped table-condensed table-bordered table-hover small">
                            <thead>
                                <tr class="lightgrey">
                                    <td><b>Employee ID</b></td>
                                    <td><b>Last Name</b></td>
                                    <td><b>First Name</b></td>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="e in employees" :key="e.EmployeeID" :value="e">
                                    <td>{{e.EmployeeCode}}</td>
                                    <td>{{e.LastName}}</td>
                                    <td>{{e.FirstName}}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
            <div class="row">
                <div class="col-sm-6">
                    <div class="form-group">
                        <button type="button" class="btn btn-sm btn-primary" @click="addEmployee()" aria-label="Add Employee">
                            <span class="fa fa-add" aria-hidden="true"> Add Employee</span>
                        </button>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import repo from "@/utils/repo"
    //import spinner from "@/components/controls/Spinner"
    export default {
        name: "Personnel",
       // components: { spinner },
        data() {
            return {
                isLoading: true,

                manager: null,

                managers: [],
                employees: [],

                selections: {
                    NodeGroupID: null,
                    UserID: null
                },
            }
        },
        mounted() {
            this.refresh();
        },
        methods: {
            getManagers() {
                return repo.employees.listManagers().then(res => {
                    this.managers = res.data.Data;
                    this.managers.sort((a, b) => (a.DisplayName.toLowerCase() > b.DisplayName.toLowerCase()) ? 1 : -1)
                    this.getEmployees();
                });
            },
            getEmployees() {
                return repo.employees.list(this.manager).then(res => {
                    this.employees = res.data.Data;
                    this.employees.sort((a, b) => (a.LastName.toLowerCase() > b.LastName.toLowerCase()) ? 1 : -1)
                });
            },
            async refresh() {
                this.isLoading = true

                this.getManagers();

                this.isLoading = false;
            },
            handleResponse(res) {

                if (res.data.Success)
                    return true;

                //ct.incite.notify.error(res.data.Message, "Error");

                return false;
            },
            managerChange() {
                this.getEmployees();
            },

        }

    };
</script>

